@import '_default.scss';

.sidebar {
    $width-expanded: 275px;
    $width-collapsed: 75px;

    z-index: 999;

    position: fixed;

    width: $width-expanded; // expanded width
    height: 100vh;

    background-color: $color-primary;
    box-shadow: 5px 0px 25px rgba(16, 20, 21, 0.2);

    transition: transform 0.5s ease;

    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: stretch;

    a {
        color: white;
        text-decoration-line: none;
    }

    .sidebar__header {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;

        width: auto;
        height: 70px;

        margin-right: 0;
        margin-left: calc(100% - $width-collapsed);
        svg {
            width: auto;
            height: 100%;
            aspect-ratio: 1;
            stroke: $color-accent;
        }

        h1 {
            width: 100%;
            height: 100%;
            background-image: url("data:image/svg+xml;base64,");
            background-size: 80% 80%;
            background-repeat: no-repeat;
            background-position: center;
        }
    }

    .sidebar__toggle {
        margin-left: auto;

        width: 100%;
        height: 50px;
        background-color: rgba(112, 114, 115, 0.15);

        -webkit-appearance: none;
        outline: none;

        cursor: pointer;

        background-image: url("data:image/svg+xml;base64,");
        background-size: $width-collapsed 100%;
        background-repeat: no-repeat;
        background-position-x: right;
    }

    .sidebar__items {
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        gap: 20px;
        padding: 0;
        margin-right: 0;
        margin-left: calc(100% - $width-collapsed);
        .sidebar-item {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: center;

            width: $width-collapsed;
            height: 50px;
            //background-color: rgba(0, 0, 255, 0.08);

            svg {
                width: 50px;
                height: 50px;
                aspect-ratio: 1;
            }

            span {
                font-size: 1.25rem;
                font-family: Tahoma, sans-serif;
            }

            &.selected {
                svg {
                    fill: $color-accent;
                }
                &:before {
                    content: "";
                    position: absolute;
                    width: 3px;
                    height: 50px;
                    left: calc(100% - $width-collapsed);
                    right: 0;

                    border-top-right-radius: 30px;
                    border-bottom-right-radius: 30px;

                    background-color: $color-accent;
                }
            }
        }

        & > *:last-child {
            margin-top: auto;
        }
    }


    // РАЗВЁРНУТОЕ МЕНЮ
    &.expanded {
        transform: translateX(0px);

        .sidebar__toggle {
            position: relative;
            width: 100%;
            background-image: url("data:image/svg+xml;base64,");
            background-size: $width-collapsed 100%;

            &:after {
                position: absolute;
                content: "Скрыть";
                color: transparentize(white, 0.8);
                font-size: 1rem;
                left: 50%;
                top: 50%;
                transform: translate(-100%, -50%);
                transition: all 0.3s ease;
            }

            &:hover {
                &:after {
                    color: transparentize(white, 0.2);
                    transform: translate(-50%, -50%);
                }
            }
        }

        .sidebar__header {
            animation: appear 1.0s ease;
            flex-direction: row-reverse;
            margin-left: 0;
            padding: 0px 20px;
            h1 {
                display: block;
            }
            svg {
                display: none;
            }
        }
        .sidebar__items {
            margin-left: 0;
            overflow: hidden;

            .sidebar-item {
                padding-left: 35px;
                animation: appear 1s ease;
                justify-content: flex-start;
                gap: 20px;
                width: 100%;

                &:active {
                    outline: none;
                    text-decoration: none;
                }
                &.selected {
                    background-color: transparentize($color-accent, 0.95);

                    &:before {
                        content: none;
                    }
                }
                svg {
                    height: 40px;
                    width: 40px;
                }
                span {
                    display: block;
                    transition: all 0.3s 0.1s ease;
                }
            }
        }
    }

    // СКРЫТОЕ МЕНЮ
    &:not(.expanded) {
        transform: translateX($width-collapsed - $width-expanded);

        .sidebar__header {
            svg {
                animation: appear 1.5s ease;
            }
            h1 {
                display: none;
            }
        }
        .sidebar__items {
            animation: appear 1.5s ease;

            .sidebar-item {
                span {
                    display: none;
                }
            }
        }
    }
}

@keyframes appear {
    from {opacity: 0; }
    to { opacity: 1; }
}

